/* eslint-disable office-addins/no-context-sync-in-loop */
/* eslint-disable office-addins/call-sync-before-read */
/* eslint-disable office-addins/load-object-before-read */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable prettier/prettier */
/* eslint-disable no-undef */

import { getColorForMove } from "./helpers";

export const applyFormula = async () => {
    try {
        await Excel.run(async (context) => {
            const sheet = context.workbook.worksheets.getActiveWorksheet();
            const formulaRange = sheet.getRange("B3:V22");

            const numRows = 20;
            const numCols = 21;
            const formulaArray = Array(numRows).fill(
                Array(numCols).fill("=CEILING.MATH((COLUMN()-2)/8) + (ROW()-3)*3")
            );

            formulaRange.formulas = formulaArray;

            const startCell = sheet.getRange("B3");
            startCell.select();

            await context.sync();
            console.log("Formula applied.");
        });
    } catch (error) {
        console.error("Error applying formula:", error);
    }
};

export const moveCell = async (rowChange: number, colChange: number, isRecording: boolean, setSavedMovements: React.Dispatch<React.SetStateAction<number[][]>>) => {
    try {
        await Excel.run(async (context) => {
            const sheet = context.workbook.worksheets.getActiveWorksheet();
            const activeCell = context.workbook.getActiveCell();
            activeCell.load("rowIndex, columnIndex, values");
            await context.sync();

            let newRow = activeCell.rowIndex + rowChange;
            let newCol = activeCell.columnIndex + colChange;

            if (newRow >= 2 && newRow <= 21 && newCol >= 1 && newCol <= 21) {
                const targetCell = sheet.getCell(newRow, newCol);
                targetCell.format.fill.color = getColorForMove(rowChange, colChange);
                targetCell.select();
                await context.sync();

                targetCell.load("values");
                await context.sync();

                let cellValue = targetCell.values[0][0];

                const b2Cell = sheet.getRange("B2");
                b2Cell.values = [[cellValue]];
                b2Cell.format.fill.color = "#00008B";
                b2Cell.format.font.color = "#FFFFFF";
                b2Cell.format.horizontalAlignment = "Center";

                await context.sync();

                if (isRecording) {
                    setSavedMovements((prev) => [...prev, [rowChange, colChange]]);
                }
            }
        });
    } catch (error) {
        console.error("Error moving cell:", error);
    }
};

export const replayMovements = async (savedMovements: number[][]) => {
    try {
        await Excel.run(async (context) => {
            const activeSheet = context.workbook.worksheets.getActiveWorksheet();
            const activeCell = context.workbook.getActiveCell();
            activeCell.load("rowIndex, columnIndex");
            await context.sync();

            let currentRow = activeCell.rowIndex;
            let currentCol = activeCell.columnIndex;

            for (const [rowChange, colChange] of savedMovements) {
                currentRow += rowChange;
                currentCol += colChange;

                if (currentRow < 2 || currentRow > 21 || currentCol < 1 || currentCol > 21) {
                    console.log("Move out of bounds, stopping replay.");
                    break;
                }

                const targetCell = activeSheet.getCell(currentRow, currentCol);
                targetCell.format.fill.color = getColorForMove(rowChange, colChange);
                targetCell.select();
                await context.sync();

                await new Promise((resolve) => setTimeout(resolve, 300));
            }

            console.log("Replay completed.");
        });
    } catch (error) {
        console.error("Error replaying movements:", error);
    }
};

export const saveMovements = async (savedMovements: number[][], setSavedMovements: React.Dispatch<React.SetStateAction<number[][]>>) => {
    if (savedMovements.length === 0) {
        console.log("No movements to save.");
        return;
    }

    try {
        await Excel.run(async (context) => {
            const sheets = context.workbook.worksheets;
            sheets.load("items/name");
            await context.sync();

            let sheetNumber = 1;
            let newSheetName = `Saved Movements ${sheetNumber}`;

            while (sheets.items.some((sheet) => sheet.name === newSheetName)) {
                sheetNumber++;
                newSheetName = `Saved Movements ${sheetNumber}`;
            }

            const newSheet = sheets.add(newSheetName);
            const movementData = savedMovements.map(([r, c]) => [`${r},${c}`]);
            newSheet.getRange(`A1:A${movementData.length}`).values = movementData;

            await context.sync();
            console.log(`Movements saved in sheet: ${newSheetName}`);

            setSavedMovements([]);
        });
    } catch (error) {
        console.error("Error saving movements:", error);
    }
};

export const clearBoard = async () => {
    try {
        await Excel.run(async (context) => {
            const sheet = context.workbook.worksheets.getActiveWorksheet();
            const formulaRange = sheet.getRange("B3:V22");
            formulaRange.format.fill.clear();

            const b2Cell = sheet.getRange("B2");
            b2Cell.values = [[""]];
            b2Cell.format.fill.clear();
            b2Cell.format.font.color = "#000000";

            const startCell = sheet.getRange("B3");
            startCell.select();

            await context.sync();
            console.log("Board cleared.");
        });
    } catch (error) {
        console.error("Error clearing board:", error);
    }
};

export const handleWinMove = async (lastMove: string, setLastMove: React.Dispatch<React.SetStateAction<string>>, moveCell: (rowChange: number, colChange: number) => void) => {
    try {
        await Excel.run(async (context) => {
            const activeCell = context.workbook.getActiveCell();
            activeCell.load("rowIndex, columnIndex");
            await context.sync();

            let newRow = activeCell.rowIndex - 1;
            let newCol = activeCell.columnIndex;
            if (lastMove === "lose") {
                newCol += 1;
            }

            if (activeCell.rowIndex === 2) {
                if (newCol < 21) {
                    newRow = activeCell.rowIndex;
                    newCol += 1;
                }
            }

            if (newRow >= 2) {
                setLastMove("win");
                moveCell(newRow - activeCell.rowIndex, newCol - activeCell.columnIndex);
            }
        });
    } catch (error) {
        console.error("Error handling win move:", error);
    }
};

export const handleLoseMove = async (lastMove: string, setLastMove: React.Dispatch<React.SetStateAction<string>>, moveCell: (rowChange: number, colChange: number) => void) => {
    try {
        await Excel.run(async (context) => {
            const activeCell = context.workbook.getActiveCell();
            activeCell.load("rowIndex, columnIndex");
            await context.sync();

            let newRow = activeCell.rowIndex + 1;
            let newCol = activeCell.columnIndex;
            if (lastMove === "win") {
                newCol += 1;
            }

            if (newRow <= 21) {
                setLastMove("lose");
                moveCell(newRow - activeCell.rowIndex, newCol - activeCell.columnIndex);
            }
        });
    } catch (error) {
        console.error("Error handling lose move:", error);
    }
};
