import { Button } from "@fluentui/react-components";
import React from "react";

const RecordBtn: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                // background: "linear-gradient(to right, #D4145A, #ff4b2b)",
                background: "linear-gradient(to left, #cc208e, #6713d2 )",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "15px",
                width: "40%",


            }}
        >
            Record Prog
        </Button>
    );
};

export default RecordBtn;
