import React from "react";
// import { ArrowUp } from "lucide-react";
import { Button } from "@fluentui/react-components";
import { ArrowAutofitContent20Filled, ArrowDownLeftFilled, ArrowDownRegular, ArrowUpFilled, ArrowUpRegular, ArrowUpRightFilled } from "@fluentui/react-icons";

const LeftDownBtn: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                // background: "linear-gradient(to right, #D4145A, #ff4b2b)",
                // background: "linear-gradient(to left, #00c6fb, #005bea )",
                background: "#156082",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "15px",
                width: "95%",
                margin: "5px 5px 0px 5px"

            }}
            icon={< ArrowDownLeftFilled />}


        >

            Go Left Down
        </Button>
    );
};

export default LeftDownBtn;
