/* eslint-disable prettier/prettier */
import { Button } from "@fluentui/react-components";
import React from "react";

const CreateTableBtn: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                background: "linear-gradient(to right, #ff7e5f, #feb47b)",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "17px",
                width: "45%",
                marginTop: "-5px"
            }}
        >
            Add Data
        </Button>
    );
};

export default CreateTableBtn;
