/* eslint-disable prettier/prettier */
import { Button } from "@fluentui/react-components";
import React from "react";

const WinBtn: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                // background: "linear-gradient(to right,rgb(152, 251, 145),rgb(8, 177, 24))",
                background: "linear-gradient(to left, #0ba360 , #13a10e )",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "15px",
                width: "40%",

            }}
        >
            Win Prog
        </Button>
    );
};

export default WinBtn;