import React from "react";
// import { ArrowUp } from "lucide-react";
import { Button } from "@fluentui/react-components";
import { ArrowAutofitContent20Filled, ArrowDownRegular, ArrowLeftFilled, ArrowUpFilled, ArrowUpRegular } from "@fluentui/react-icons";

const LeftButton: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                // background: "linear-gradient(to right, #D4145A, #ff4b2b)",
                // background: "linear-gradient(to left, #673AB7, #512DA8 )",
                background: "#087670",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "15px",
                width: "95%",


            }}
            icon={< ArrowLeftFilled />}

        >

            Go Left
        </Button>
    );
};

export default LeftButton;
