/* eslint-disable prettier/prettier */
import { Button } from "@fluentui/react-components";
import React from "react";

const ReplayBtn: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                background: "linear-gradient(to right, #330867, #30cfd0)",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "15px",
                width: "40%",

            }}
        >
            Replay Prog
        </Button>
    );
};

export default ReplayBtn;