import React from "react";
// import { ArrowUp } from "lucide-react";
import { Button } from "@fluentui/react-components";
import { ArrowAutofitContent20Filled, ArrowDownRegular, ArrowRightFilled, ArrowUpFilled, ArrowUpRegular } from "@fluentui/react-icons";

const RightButton: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                // background: "linear-gradient(to right, #D4145A, #ff4b2b)",
                // background: "linear-gradient(to left, #FFB75E, #ED8F03 )",
                background: "#087670",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "15px",
                width: "95%",
                margin: "5px 0px"

            }}
            icon={< ArrowRightFilled />}
            iconPosition="after"
        >

            Go Right
        </Button>
    );
};

export default RightButton;
