/* eslint-disable prettier/prettier */
export const getColorForMove = (rowChange: number, colChange: number) => {
    const colorMap: Record<string, string> = {
        "-1,0": "#FFCDD2",
        "1,0": "#C8E6C9",
        "0,-1": "#BBDEFB",
        "0,1": "#FFF9C4",
        "-1,-1": "#E1BEE7",
        "-1,1": "#FFCC80",
        "1,-1": "#B2EBF2",
        "1,1": "#DCE775"
    };
    return colorMap[`${rowChange},${colChange}`] || "#FFFFFF";
};