import { Button } from "@fluentui/react-components";
import React from "react";

const SelReplayBtn: React.FC<{ onClick: () => void }> = ({ onClick }) => {
    return (
        <Button
            onClick={onClick}
            style={{
                // background: "linear-gradient(to right,rgb(152, 251, 145),rgb(8, 177, 24))",
                background: "#8032bb",
                color: "white",
                // padding: "10px 0px",
                border: "none",
                borderRadius: "25px",
                cursor: "pointer",
                fontSize: "15px",
                width: "60%",
                margin: "5px",
                padding: "5px"


            }}
        >
            Select Sheet Replay
        </Button>
    );
};

export default SelReplayBtn;